*{
    font-family: 'Montserrat', sans-serif;
}
.button:hover {
  background-color: var(--secondary-color-hover);
}

.button:active {
  background-color:#eb3349;
}

.button svg {
  margin-left:10px;
}


.button-outer {
  padding:6px;
  display: inline-block;
  cursor: pointer;
  margin-bottom:10px;
}

.button-inner {
    background: linear-gradient(#FF0000, #990000);
  padding: 25px 45px;
  color: #fff;
  text-transform: uppercase;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 2px;
    border-radius:4px;
    border: none;
  margin: 0;
  cursor: pointer;


}

.button-inner:focus {
    outline: none
}

.button-inner:hover {
  background-image: linear-gradient(to top,  #FF0000, #990000);
}

.button-inner:active {
  background-image: linear-gradient(to top,  #FF0000, #990000);
}

.button-inner svg {
  margin-left:10px;
}

.button-outer.empty .button-inner {
  width:100%;
  height:100%;
  padding: 0;
}

.button-outer.fullwidth .button-inner {
  width:100%;
  padding-right:5px;
  padding-left: 5px;
}

.button-outer.outline {
  box-shadow: inset 1px 1px 0px 0px #ffffff22, inset 0px 0px 0px 5px #eb3349;
}

.button-outer.outline .button-inner {
  background-image: none;
  box-shadow: none;
  background-color: transparent;
  padding-top:17px;
  padding-bottom: 17px;
  text-shadow: none;
}

.button-outer.outline:hover {
  background-color: #eb3349;
}

.close-btn {
  width:60px;
  height:60px;
  position: absolute;
  right:-30px;
  top:-30px;
  border-radius: 50%;
  cursor: pointer;
}


.close-btn:after, .close-btn:before {
  content: '';
  width:2px;
  height:20px;
  background-color: #fff;
  position: absolute;
  top: 19px;
  left: 29px;
  border-radius: 5px
}

.close-btn:after {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.close-btn:before {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.button-outer.empty.icon-only {
  width:60px;
  height:60px;
}

.button-outer.empty.icon-only .button-inner svg {
    font-size: 26px;
    text-align: center;
    padding: 0;
    margin: 0;
}

.button-outer.disabled .button-inner {
    background-color: #1d2350;
    background-image: none;
    color: #909090;
    text-shadow: none;
    box-shadow: none;
    cursor: default;
}



/* class applies to select element itself, not a wrapper element */
.select-css {
    display: block;
    font-size: 36px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    line-height: 1.3;
    padding: 0;
    width: 100%;
    max-width: 100%; /* useful when width is set to anything other than 100% */
    box-sizing: border-box;
    margin: 0;
    border: none;
    box-shadow: none;
    -webkit-appearance: none;
            appearance: none;
    background-color: transparent;
    outline: none;
    padding-right: 20px;
}

/* Hide arrow icon in IE browsers */
.select-css::-ms-expand {
    display: none;
}
/* Hover style */
.select-css:hover {
    border-color: #888;
}
/* Focus style */
.select-css:focus {
    border-color: #aaa;
    color: #222; 
    outline: none;
}

/* Set options to normal weight */
.select-css option {
    font-weight:normal;
}

